import Vue from 'vue';
import Vuex from 'vuex';
// import * as getters from './getters.js'

Vue.use(Vuex);

/** 状态定义 */

const store = new Vuex.Store({
  state: {
    count: 0,
  },
  mutations: {
    increment: (state) => {
      const obj = state;
      obj.count += 1;
    },
    decrement: (state) => {
      const obj = state;
      obj.count -= 1;
    },
  },
});

export default store;



